<template>
  <div class="newslist">
    <div class="block">
      <el-tabs type="border-card">
        <el-tab-pane v-for="(label, index) in labels" :key="index" :label="label">
          <el-timeline>
            <el-timeline-item
              v-for="(news, index) in newsLine"
              :key="index"
              v-show="news.label === label"
              :timestamp="news.date"
              placement="top"
              color="skyblue"
            >
              <el-card>
                <h4>
                  <a :href="news.ul" target="_blank" class="newslink">
                    {{ news.title }}</a
                  >
                </h4>
                <p>{{ news.abstra }}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    filename: String
  },
  data() {
    return {
      newsLine: [],
      tabPosition: "top",
      labels: []
    };
  },
  created() {
    var _this = this;
    var url = "newsdata/" + _this.filename + ".csv";
    this.$http.get(url).then(res => {
      const d3 = require("d3-dsv");
      _this.newsLine = d3.csvParse(res.data);
      var items = [];
      for (let item of _this.newsLine) {
        items.push(item.label);
      }
      var s = new Set(items);
      for (let item of s) {
        _this.labels.push(item);
      }
    });
  },
  methods: {
    selectLabel(item) {
      for(let news of newsLine){
        if(news.label == item){
          this.labelNews.push(item)
        }
      }
    }
  }
};
</script>
<style>
.newslist {
  /* margin-top: 50px; */
  margin-left: 5px;
}
.newslink {
  text-decoration: none;
  color: #666;
}
.newslink:hover {
  color: blue;
}
</style>
