<template>
  <div class="echartLayout">
    <div id="container" style="width:600px; height:500px; "></div>
  </div>
</template>

<script>
import echarts from "echarts";
//   import imgSrc from '../../assets/img/echar_person.png'
export default {
  name: "personRelation",
  props: {
    filename: String
  },
  data() {
    return {
      myChart: null,
      resData: [],
      chartData: [],
      chartLink: []
    };
  },
  mounted() {
    // this.$http.get("newsdata/data.json").then(res => {
    //   this.infodata = res.data;
    //   console.log(this.infodata)
    // });

    this.loadData();
  },
  methods: {
    initEchart() {
      let dom = document.getElementById("container");
      this.myChart = echarts.init(dom);
      // this.chartData = this.dataEChart();
      // this.chartLink = this.linkEChart();
      let option = {
        tooltip: {
          show: false
        },
        series: [
          {
            type: "focusNodeAdjacency",
            edgeLabel: {
              normal: {
                formatter: "{c}",
                show: true
              }
            },
            
            edgeSymbol: ["circle", "arrow"],
            force: {
              repulsion: 300
            },
            layout: "force",
            roam: true,
            focusNodeAdjacency: true,
            lineStyle: {
              normal: {
                width: 2,
                color: "#666"
              }
            },
            itemStyle: {
              normal: {
                color: "blue"
              },
              //
              emphasis: {
                focus: "adjacency",
                shadowColor: "#666",
                // shadowOffsetX: 0,
                // shadowOffsetY: 0,
                shadowBlur: 20
              }
            },
            label: {
              normal: {
                show: true
              }
            },
            categories: [
              {
                name: "0",
                itemStyle: {
                  color: "#1b315e"
                },
                // symbolSize: 80
              },
              {
                name: "1",
                itemStyle: {
                  color: "#2468a2"
                },
                // symbolSize: 65
              },
              {
                name: "2",
                itemStyle: {
                  color: "#33a3dc"
                },
                // symbolSize: 55
              },
              {
                name: "3",
                itemStyle: {
                  color: "#00a6ac"
                },
                // symbolSize: 50
              }
            ],
            //
            //   symbol: `image://${imgSrc}`,
            symbolSize: 50,
            type: "graph",
            links: this.chartLink,
            data: this.chartData,
            // label: {
            //   //
            //   normal: {
            //     show: true, //
            //     position: "right", //
            //     //
            //     formatter: function(params) {
            //       return params.data.label;
            //     }
            //   }
            // }
          }
        ]
      };
      this.myChart.setOption(option);
      // this.myChart.on("click", function(params) {
      //   console.log(params.data); //
      // });
    },
    /**
     * 
     */
    // dataEChart() {
    //   var node
    // this.$http.get("newsdata/node.json").then(function(res) {
    //   node = res.data;
    // })
    // console.log(node)
    // return node;
    // },
    /**
     * 
     */
    // linkEChart() {
    //   var _this = this
    //   this.$http.get("newsdata/data.json").then(function(res) {
    //   return res.data[1].link;
    //   // console.log(_this.chartLink)
    // });
    // // return _this.chartLink
    // },
    loadData() {
      var _this = this;
      var url = "newsdata/" + _this.filename + ".json";
      this.$http.get(url).then(function(res) {
        _this.chartLink = res.data[1].link;
        _this.chartData = res.data[0].node;
        // console.log(_this.chartLink)
        _this.initEchart();
      });
    }
  }
};
</script>

<style scoped>
/* .echartLayout {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  } */
.echartLayout {
  position: fixed;
  top: 30px;
  right: 20px;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.08);
}
</style>